import React, { lazy, Suspense, useEffect, useState } from "react";
import { Avatar, Flex, HStack, Spacer, Spinner, Stack, Text } from "@chakra-ui/react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { CategorySvg } from "../../../assets/svg/svg";
import { nanoid } from "nanoid";
import { dummy_tokens } from "../../../constants/tokenJSON";
import { useSelector } from "react-redux";
import { AuthReducerProps } from "../../../interfaces";
import { RootState } from "../../../redux/store";
import { useGetAllOffersQuery } from "../../../redux/rtkQuery/api";
import OffersCategory from "../../../bulkComponents/OffersCategory";
import Images_Icons from "../../../constants/icons-images";
import Container from "../../../components/Container";
import SkeletonLoader from "../../../components/SkeletonLoader";
import LoadingOverlay from "../../../components/LoadingOverlay";
import { useDashboardStyles } from "../Dashboard/styles";
import { GLOBAL_PREFIX } from "../../../constants";

const Footer = lazy(() => import("../../../components/Footer"));
const DashboardProductItem = lazy(() => import("../../../components/DashboardProductItem"));
const Breadcrumbs = lazy(() => import("../../../components/Breadcrumbs"));
const BrandsFilters = lazy(() => import("../../../components/BrandsFilter"));
const SizeFilters = lazy(() => import("../../../components/SizesFilter"));
const PriceFilters = lazy(() => import("../../../components/PriceFilter"));
const SortFilters = lazy(() => import("../../../components/SortFilters"));
const MeProtocolButton = lazy(() => import("../../../components/MeProtocolButton"));
// const DashboardNavBar = lazy(
//   () => import("../../../components/DashboardNavBar")
// );
const NewDashboardNavBar = lazy(() => import("../../../components/NewDashboardNavBar"));

const DashboardCategoriesFilter = () => {
  const { root, loadMore } = useDashboardStyles();

  const { id } = useParams();
  const { state }: any = useLocation();

  let navigate = useNavigate();

  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);

  const [page, setPage] = useState<number>(1);

  const { data, isError, isFetching, isLoading, isSuccess }: any = useGetAllOffersQuery({
    page,
    limit: 8,
    category_id: state.id,
  });

  // useEffect(() => {
  //   console.log(state, "OIOPIPP");
  // }, []);

  return (
    <Flex flexDir={"column"}>
      {/* ----------------------------------------------------------------------NAV BAR------------------------------------------------------------ */}
      <Suspense fallback={<Spinner size="sm" />}>
        {/* <DashboardNavBar /> */}
        <NewDashboardNavBar />
      </Suspense>
      <Container>
        <Stack {...root}>
          <Suspense fallback={<Spinner size="sm" />}>
            <Breadcrumbs
              breadcrumbs={[
                { name: "Home", link: `${GLOBAL_PREFIX}/dashboard` },
                {
                  name: "Categories",
                  link: `${GLOBAL_PREFIX}/dashboard/category`,
                },
                {
                  name: id?.replaceAll("_", " "),
                  link: `${GLOBAL_PREFIX}/dashboard/category/${id}`,
                  isCurrentPage: true,
                },
              ]}
            />
          </Suspense>

          {/* ----------------------------------------------------------------------TOP CARD------------------------------------------------------------ */}
          <HStack bg={"grey"} h={"480px"} w={"100%"} borderRadius={"16px"} overflow={"hidden"}>
            <Stack w={"60%"} h={"100%"} p={"20px"}>
              <Text fontSize={"32px"}>{id?.replaceAll("_", " ")}</Text>
              <Text fontSize={"32px"}>Back in touch with nature.</Text>
              <Spacer />

              <Flex flexDir={"row"}>
                {dummy_tokens.slice(0, 6)?.map((e, i) => (
                  <Avatar key={nanoid()} mr={"10px"} name="Token" src={e.img} size={"md"} />
                ))}
              </Flex>

              <Text fontSize={"20px"} w={"85%"} noOfLines={4}>
                {state?.description}
              </Text>

              <Suspense fallback={<Spinner size="sm" />}>
                {" "}
                <MeProtocolButton name={"Explore offers"} rest={{ w: "146px", fontSize: "16px" }} />
              </Suspense>
            </Stack>
            <Flex w={"50%"} h={"100%"}>
              <CategorySvg img={state?.banner || Images_Icons.blackPlaceholder} />
            </Flex>
          </HStack>

          {/* ----------------------------------------------------------------------FILTERS------------------------------------------------------------ */}

          <Flex w={"100%"} justifyContent="space-between">
            <Suspense fallback={<Spinner size="sm" />}>
              <HStack>
                <BrandsFilters />
                <SizeFilters />
                <PriceFilters />
              </HStack>

              <HStack>
                <SortFilters />
              </HStack>
            </Suspense>
          </Flex>

          {isFetching ? (
            <SkeletonLoader />
          ) : (
            <>
              <OffersCategory
                categoryName={state.name}
                category_id={state.id}
                HeaderShown={false}
                loaded={!isFetching}
              />
            </>
          )}
        </Stack>
      </Container>

      <Suspense fallback={<Spinner size="sm" />}>
        <Footer />
      </Suspense>
      {isFetching && <LoadingOverlay />}
    </Flex>
  );
};

export default DashboardCategoriesFilter;
