import {
  Flex,
  Spacer,
  Spinner,
  SimpleGrid,
  Center,
  Button,
  Icon,
  Stack,
  Text,
  useToast,
  Skeleton,
  Heading,
  HStack,
} from "@chakra-ui/react";
import { nanoid } from "@reduxjs/toolkit";
import axios from "axios";
import moment from "moment";
import { FC, lazy, Suspense, useEffect, useState } from "react";
import { FaAngleDown } from "react-icons/fa";
import { useNavigate } from "react-router-dom";
import { PopularDeaslsSvg } from "../../assets/iconsSvg/iconsSvg";
import Container from "../../components/Container";
import DashboardProductItemOld from "../../components/DashboardProductItemOld";
// import DashboardProductItem from "../../components/DashboardProductItem";
// import MeProtocolButton from "../../components/MeProtocolButton";
import { BASE_URL } from "../../config";
import { GLOBAL_PREFIX } from "../../constants";
import { useDashboardStyles } from "../../pages/DashboardModule/Dashboard/styles";
import { OfferDataProps } from "../../interfaces/responseTypes";
// import * as api from "../../redux/rtkQuery/api";

const MeProtocolButton = lazy(() => import("../../components/MeProtocolButton"));
const DashboardProductItem = lazy(() => import("../../components/DashboardProductItem"));

interface OffersCategoryProps {
  categoryName: string;
  category_id: string;
  HeaderShown?: boolean;
  rest?: any;
  loaded?: any;
}

const OffersCategory: FC<OffersCategoryProps> = ({
  categoryName,
  category_id,
  HeaderShown = true,
  rest,
  loaded,
}): JSX.Element | null => {
  let navigate = useNavigate();
  let toast = useToast();

  const { nameStyles, button, loadMore } = useDashboardStyles();
  // const [isLoaded, setIsLoaded] = useState<boolean>(true);
  const [page, setPage] = useState<number>(1);
  const [loadedOffers, setLoadedOffers] = useState<Array<OfferDataProps>>([]);
  const [loadingMore, setLoadingMore] = useState<boolean>(false);

  const [reachedEnd, setReachedEnd] = useState<boolean>(false);
  const [totalPages, setTotalPages] = useState<number>(1);
  const [offersFetching, setOffersFetching] = useState<boolean>(false);

  // const { data }: any = useGetAllOffersQuery(
  //   { page, limit: 8, category_id },
  //   { refetchOnMountOrArgChange: true }
  // );

  useEffect(() => {
    // console.log(category_id);

    GET_CATEGORIES_OFFERS();
  }, [page]);

  // useEffect(() => {
  //   LoadMore();
  // }, [page, data]);

  // function LoadMore() {
  //   setLoadingMore(true);

  //   try {
  //     setLoadedOffers([...loadedOffers, ...data.data.offers]);
  //   } catch (e) {
  //     console.log(e, "LoadMoreErr");
  //   } finally {
  //     setLoadingMore(false);
  //   }
  // }

  // --------------------------------------------------------------NEW FETCH IMPLEMENTATION---------------------------------------------------------------------------

  async function GET_CATEGORIES_OFFERS() {
    setOffersFetching(true);
    setReachedEnd(false);
    try {
      const {
        data,
      }: {
        data: {
          data: { offers: OfferDataProps[] };
        };
      } = await axios.get(`${BASE_URL}/store/offer?page=${page}&limit=8&category=${category_id}`);

      if (data?.data?.offers.length < 1) {
        setReachedEnd(true);
        // MeProtocolToast(toast, "No offer available", 3000, "top-right");
      }
      setLoadedOffers([...loadedOffers, ...data?.data?.offers]);
      // setTotalPages(data?.data?.total);
      // console.log(data?.data?.offers);
    } catch (e: any | any | unknown) {
      console.log(e, "GET_CATEGORIES_OFFERS_ERR");
    } finally {
      setOffersFetching(false);
    }
  }

  if (loadedOffers?.length <= 0) {
    return null;
  }

  return (
    <Skeleton borderRadius={"20px"} isLoaded={loaded}>
      <Stack>
        {HeaderShown && (
          <Flex w={"100%"} pt={"20px"}>
            <HStack>
              <PopularDeaslsSvg />
              <Heading {...nameStyles}>{categoryName}</Heading>
            </HStack>
            <Spacer />
            <Suspense fallback={<Spinner size="sm" />}>
              <Center
                bg="dark"
                color="white"
                px="15px"
                py="5px"
                as="button"
                onClick={() =>
                  navigate(`${GLOBAL_PREFIX}/dashboard/category/${categoryName}`, {
                    state: rest,
                  })
                }
                {...nameStyles}
              >
                SEE ALL OFFERS
              </Center>
            </Suspense>
          </Flex>
        )}
        <SimpleGrid
          columns={[2, 2, 3, 4, 4]}
          spacingX={["35px", "35px", "35px", "15px", "35px"]}
          w={"100%"}
        >
          <Suspense fallback={<Spinner size="sm" />}>
            {loadedOffers?.map((e, i: number) => (
              <DashboardProductItem
                key={nanoid()}
                isLoaded={!offersFetching}
                productTitle={e.name}
                points={`${e?.tokens}${e.reward?.rewardSymbol}`}
                productSubtitle={e.product?.category?.name}
                price={e.originalPrice}
                isNew={moment(moment()).diff(e?.createdAt, "days") < 1}
                // trending={e.trending}
                rest={{ ...e }}
                i={i}
                bgImage={e?.offerImages}
                onClick={() =>
                  // navigate(`${GLOBAL_PREFIX}/productDetails/${e.slug}`, { state: { ...e } })
                  navigate(`${GLOBAL_PREFIX}/productDetails/${e.offerCode}`)
                }
              />
            ))}
          </Suspense>
        </SimpleGrid>
        {/* LOAD MORE BUTTON */}
        {/* {totalPages !== page || */}
        {!reachedEnd && (
          <Center
            as={Button}
            {...loadMore}
            onClick={() => {
              setPage(page + 1);
            }}
          >
            {offersFetching ? (
              <Spinner size={"sm"} />
            ) : (
              <>
                Load more <Icon as={FaAngleDown} ml={"5%"} />
              </>
            )}
          </Center>
        )}
      </Stack>
    </Skeleton>
  );
};

export default OffersCategory;
